// 
export function joinSmallTV(roomId, tvId) {
  return this.get({
    uri: 'SmallTV/join',
    params: {
      roomId: roomId,
      id: tvId,
      _: new Date().getTime()
    }
  }).then(res => {
    let data = JSON.parse(res)
    return data
  })
}

// 
export function getSmallTVReward(tvId) {
  return this.get({
    uri: 'SmallTV/getReward',
    params: {
      id: tvId,
      _: new Date().getTime()
    }
  }).then(res => {
    let data = JSON.parse(res)
    return data
  })
}



